.header {
    display: flex;
    white-space: nowrap;
    box-sizing: border-box;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 500;
    width: 100%;
    padding: 0 2rem;
    background: var(--accent-background);
    color: var(--text);
    box-shadow: 0 .2rem .4rem #0004;
}

.switch {
    margin-right: 1em;
    font-size: 1.2rem;
    padding: .5rem;
    background-color: var(--accent-background);
    color: var(--text);
    border-radius: .3rem;

    &:hover {
        background: var(--accent-background-brighter);
        box-shadow: 0 0 1rem .2rem var(--accent-background-brighter);
    }
}

.icon {
    margin: 0 .4em;
}

@media screen and (max-width: 700px) {
    .header {
        padding: .3rem 3%;
    }
    h1 {
        font-size: 1.5em;
    }
    .switch{
        font-size: .8rem;
    }
}