.controls {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 2rem;
    align-items: center;
}

.select {
    flex: 1;
    flex-basis: 14rem;
    max-width: 18rem;
    z-index: 10;
}

.search {
    flex: 1;
    flex-basis: 20rem;
    max-width: 32rem;
}
